.page {
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 30px;
}

.page.min {
  max-width: 900px;
}

#banner-content {
  text-align: center;
}

#banner {
  max-width: 100%;
  position: relative;
  margin: 0 auto;
  text-align: center;
}

#home-link-title {
  margin-top: 15px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  text-align: left;
  color: var(--text-color);
  background: rgb(1, 102, 102);
}

#home-link-container {
  margin: 0 10px 10px 10px;
  border: 1px solid rgb(1, 102, 102);
}

.home-link {
  cursor: pointer;
  border-radius: 5px;
  padding: 12px 18px;
  text-align: left;
  text-decoration: underline;
}

#search-wrapper {
  position: relative;
  top: 1px;
  max-width: 410px;
  padding: 8px;
  margin: 50px 0 0 0;
}

#search-student {
  width: 100%;
  height: 35px;
  font-size: 15px;
  padding: 0 15px 0 35px;
  outline: none;
  border: transparent;
  border-radius: 50px;
  border: 1px solid var(--border-color);
  color: var(--text-color);
  background: var(--btn-bg);
}

#search-student:focus {
  border: 2px solid rgb(1, 102, 102);
}

.details {
  overflow: hidden;
  padding: 5px 10px;
  display: block;
  margin: 20px 10px;
  transition: all 0.15s linear;
  border-radius: var(--global-border-radius);
  ;
  border: 1px solid var(--border-color);
}

.details.hide {
  display: none;
}

.details-title {
  float: left;
  user-select: none;
  font-weight: bold;
  font-size: 17px;
  color: white;
  padding: 5px 15px;
  margin-bottom: 0px;
  border-radius: 20px;
  background: rgb(1, 102, 102);
}

.details[open] .details-title {
  margin-bottom: 10px;
}

.table-scroll {
  width: 100%;
  overflow: auto;
  overflow-y: hidden;
}

.group-result {
  display: block;
  padding: 15px 0 10px;
  color: var(--text-color);
}

.group-result.hide {
  display: none;
}

.group-result-text {
  color: red;
  font-weight: bold;
  margin: 0 5px;
}

#select-table-content {
  position: relative;
  width: 100%;
  text-align: center;
}

.groups {
  margin: 20px 10px 10px 10px;
  padding: 10px;
  border-radius: var(--global-border-radius);
  ;
  box-shadow: 0 1px 2px rgb(0 0 0 / 20%);
  background: var(--btn-bg);
}

#group-search {
  padding: 0 10px;
}

.groups-title {
  text-align: left;
  font-size: 17px;
  font-weight: 500;
  padding: 0px 0 20px;
  color: var(--text-color);
}


/* Profile page */

.profile {
  max-width: 820px;
  margin: 0 auto;
}

.profile-bg {
  position: relative;
  width: calc(100% - 110px);
  height: 250px;
  overflow: hidden;
}

.profile-img {
  width: 100%;
  height: 300px;
  border-radius: calc(var(--global-border-radius) * 2);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 75%;
  background-image: url(../image/ProfileBG.png);
}

.profile-content {
  z-index: 9;
  position: relative;
}

.profile-section {
  position: relative;
  margin: 10px;
  top: -55px;
}

.profile-top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
}

.profile-id {
  position: relative;
  font-size: 18px;
  font-weight: bold;
  text-align: right;
  color: var(--text-color);
}

.profile-btn-content {
  display: block;
}

.profile-btn-content.hide {
  display: none;
}

.profile-button {
  color: white;
  cursor: pointer;
  border: none;
  padding: 6px 10px;
  margin: 5px 0;
  font-size: 15px;
  border-radius: 5px;
  background: green;
}

#profile-in-favorites {
  font-size: 14px;
  font-weight: 500;
}

.profile-main {
  position: relative;
  margin: 0 auto;
  top: 35px;
  max-width: 800px;
  color: var(--text-color);
}

#warning {
  display: flex;
  align-items: center;
  margin: 10px 0px 20px;
  padding: 15px;
  font-size: 15px;
  color: #996A13;
  border: 1px solid #FFB020;
  border-radius: var(--global-border-radius);
  background: rgba(255, 176, 32, 0.1)
}

.fa-exclamation-triangle{
  color: #FFB020;
  margin-right: 15px;
}

@media screen and (max-width: 550px) {
  .profile-section {
    top: -20px;
    margin: 10px;
  }

  .profile-bg {
    height: 200px;
  }

  .profile-parallax {
    top: -27px;
  }
}

#tiles-content {
  display: flex;
}

.tiles {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 50%;
  padding: 20px 10px;
  text-decoration: none;
  color: var(--text-color);
  border-radius: var(--global-border-radius);
  ;
  border: 2px solid rgba(1, 102, 102, 0.8);
  background: rgba(1, 102, 102, 0.15);
}

.tiles.space {
  margin-right: 10px;
}

.tiles.min {
  width: 35%;
}

.tiles.max {
  width: 70%;
}

.tiles:hover {
  color: white;
  background: rgb(1, 102, 102);
}

.tiles-title {
  padding: 5px 5px;
  font-size: 17px;
  font-weight: bold;
}

.tiles-text {
  font-size: 14px;
}

.fa-external-link-alt {
  padding-left: 10px;
}

@media screen and (max-width: 550px) {
  .tiles-title {
    padding: 5px 5px;
    font-size: 10px;
    font-weight: bold;
  }

  .tiles-text {
    font-size: 8px;
  }

  .fa-external-link-alt {
    padding-left: 10px;
    font-size: 10px;
  }
}

/* Download page */

#mobile-app-content {
  position: relative;
  top: 35px;
  display: flex;
  margin: 10px;
}

#mobile-content-left {
  width: calc(100% - 280px);
}

#mobile-title {
  font-size: 30px;
  font-weight: 600;
  color: var(--text-color);
}

#mobile-download {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  margin: 15px 0 8px;
  border-radius: 5px;
  background: rgb(1, 102, 102);
}

#mobile-download a {
  text-decoration: none;
  color: white;
}

#mobile-desc {
  font-size: 14px;
  color: var(--text-color);
}

#mobile-content-right {
  width: 300px;
}

#mobile {
  position: relative;
  width: 280px;
  height: 550px;
  border: 10px solid black;
  border-radius: 44px;
  overflow: hidden;
  background: rgb(242, 242, 242);
}

#mobile-status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: cacl(100% - 42px);
  height: 30px;
  padding: 0 21px;
}

#mobile-bar-clock {
  width: 30px;
  height: 8px;
  border-radius: 50px;
  background: rgb(184, 184, 184);
}

#mobile-bar-content {
  display: flex;
}

#mobile-bar-power {
  width: 15px;
  height: 8px;
  margin: 0 5px;
  border-radius: 50px;
  background: rgb(184, 184, 184);
}

#mobile-bar-percent {
  width: 8px;
  height: 8px;
  border-radius: 50px;
  background: rgb(184, 184, 184);
}

#mobile-nav-bar {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  left: 0;
  right: 0;
  bottom: 0;
  height: 45px;
  padding: 0 10px;
  border-top: 1px solid rgb(197, 197, 197);
  background: rgb(232, 232, 232);
}

#mobile-bar-btn {
  position: relative;
  top: -3px;
  width: 20px;
  height: 20px;
  border-radius: 30px;
  background: rgb(184, 184, 184);
}

#mobile-bar-btn::after {
  content: "";
  position: absolute;
  left: -5px;
  bottom: -8px;
  border-radius: 5px;
  width: 30px;
  height: 5px;
  background: rgb(202, 202, 202);
}

#mobile-routing {
  display: flex;
  align-items: center;
  padding-left: 10px;
  height: 35px;
  margin-bottom: 20px;
  background: rgb(226, 226, 226);
}

#mobile-routing::after {
  content: "";
  position: absolute;
  width: 110px;
  height: 11px;
  border-radius: 5px;
  background: rgb(192, 192, 192);
}

.mobile-list {
  display: flex;
  align-items: center;
  width: calc(100% - 20px);
  height: 35px;
  margin: 10px;
  border-radius: 5px;
  background: rgb(224, 224, 224);
}

.mobile-list::after {
  content: "";
  position: absolute;
  left: 20px;
  width: 150px;
  height: 15px;
  border-radius: 50px;
  background: rgb(202, 201, 201);
}

#test-content {
  position: relative;
  top: 45px;
  margin: 0 20px;
}

#test {
  display: flex;
  align-items: center;
  padding: 5px;
  font-style: italic;
  color: var(--text-color);
}

#test::before {
  content: "";
  position: absolute;
  left: -5px;
  width: 5px;
  height: 5px;
  border-radius: 5px;
  background: var(--text-color);
}

.fa-search {
  position: absolute;
  top: 18px;
  left: 20px;
  color: gray;
}

/* About page */

#about {
  margin: 10px;
}

#about-img {
  width: 100%;
  height: 300px;
  background: orange;
}

#about-title {
  padding: 30px 0 10px;
  font-weight: bold;
  border-bottom: 1px solid var(--border-color);
  color: var(--text-color);
}

/* Admin page */
#admin-secure {
  display: none;
  position: relative;
  top: 30px;
  text-align: center;
}

#admin-sign-in {
  width: 100%;
  height: 65px;
  display: block;
  font-size: 30px;
  color: var(--text-color);
}

#admin-top-menu {
  width: 100%;
  margin-bottom: 30px;
}

#admin-main-content {}

.admin-groups {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: calc(100% - 44px);
  margin: 15px 12px;
  padding: 10px;
  border-radius: var(--global-border-radius);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  background: var(--btn-bg);
}

.admin-groups.hide {
  display: none;
}

.admin-groups-title {
  width: 100%;
  padding: 0 10px;
  color: var(--text-color);
}

#admin-add-new {
  cursor: pointer;
  margin: 10px;
  font-size: 18px;
  padding: 8px 15px;
  border: none;
  color: white;
  border-radius: var(--global-border-radius);
  background: green;
}

#admin-add-new:hover {
  background: rgb(23, 88, 23);
}

#admin-news {
  width: calc(100% - 32px);
  border: 1px solid rgb(228, 228, 228);
  border-radius: var(--global-border-radius);
  background: var(--btn-bg);
  padding: 20px;
}

#admin-news-title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 20px;
  letter-spacing: 2px;
}

#admin-news-text {
  text-align: justify;
}

#admin-news-date {
  margin-top: 10px;
  font-weight: 600;
}

.admin-add-note {
  width: 100%;
  font-size: 14px;
  color: var(--description);
  padding: 5px 20px;
}

#admin-update-list {
  cursor: pointer;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: var(--global-border-radius);
  background: rgb(0, 153, 255);
}

#admin-update-list:hover {
  background: rgb(11, 135, 218);
}

.admin-update-btn-cont {
  display: flex;
  justify-content: right;
  width: 100%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid var(--border-color);
}

#admin-list-count {
  padding: 15px 0;
  color: var(--text-color)
}

.admin-input-content {
  display: block;
  position: relative;
  margin: 0 auto;
  max-width: 300px;
  text-align: left;
  padding: 5px;
}

.admin-input-content label {
  display: block;
  text-align: left;
  padding: 5px 0 5px 5px;
  color: var(--text-color);
}

.admin-input-content input {
  text-align: left;
  width: 100%;
  box-sizing: border-box;
  border-radius: var(--global-border-radius);
  border: none;
  font-size: 18px;
  padding: 8px 10px;
  color: var(--text-color);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  background: var(--btn-bg);
}

#test-btn-admin {
  cursor: not-allowed;
  margin: 10px;
  padding: 5px 15px;
  color: white;
  border-radius: var(--global-border-radius);
  font-size: 19px;
  border: none;
  background: rgb(1, 102, 102);
}

@media screen and (max-width: 1150px) {}

/* Not Found 404 page*/

#not-found {
  position: relative;
  top: 35px;
  display: block;
  text-align: center;
}

#not-found-title {
  font-size: 35px;
  font-weight: bold;
  letter-spacing: 1px;
  color: var(--text-color);
}

#not-found-text {
  font-size: 55px;
  padding: 15px 0;
  color: var(--text-color);
}

#not-found-btn {
  color: var(--text-color);
}

/* Favorites page */

.favorites-title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 15px;
}

.favorites-btn-delete {
  cursor: pointer;
  border-radius: 5px;
  border: none;
  color: white;
  padding: 8px 15px;
  font-weight: bold;
  background: red;
}

.favorites-btn-delete:hover {
  background: rgb(169, 23, 23);
}

.favorites-list-empty {
  text-align: center;
  font-size: 17px;
  letter-spacing: 1px;
}